<!-- glance 店铺购物车单页模板 -->
<template name="glanceShopcart">
	<view class="glance-shop-cart" style="background-color: #F5F5F5;" @touchmove="scrollhoming" @touchend="scrollhomed">
		<!-- 我是分割 -->
		<view style="height: 10px;background-color: #F5F5F5;"></view>

		<!-- 空购物车背景 -->
		<view v-if="shownullcart" style="width: 100%;height: 250px;">
			<view class="glance-shop-cart-nullcart"></view>
			<view style="height: 30px;width: 100%;font-size: 15px;line-height: 30px;text-align: center;">您的购物车为空，快去添加心爱的商品吧！</view>
		</view>

		<!-- 购物车商品 -->
		<view v-for="(thiscart,index) in cart" :key="index" style="background-color: #FFFFFF;">
			<!-- 营销标签 加购 换购或其他 -->
			<view style="height: 30px;line-height: 30px; width: 100%;font-size: 15px;" v-if="thiscart.groupkey && cntitems >0"
			 @click="clickgroupkey(thiscart.groupkey)">{{ thiscart.groupkey }}</view>
			<!-- 按营销活动分组的商品 -->
			<view v-for="(item,i) in thiscart.items" :key="i">
				<scroll-view style="width: 100%;white-space: nowrap;" scroll-x="true" :scroll-left='scrollposition'
				 scroll-with-animation="true" v-if="item.id > -99">
					<view class="glance-shop-cart-scrollx-items" style="display: inline-block;width: 100%;">
						<view class="glance-shop-cart-scrollx-items-item">
							<!-- 勾选 -->
							<view style="width: 10%;height: 100%;background-color: #FFFFFF;">
								<!-- 勾选 -->
								<view class="glance-shop-cart-scrollx-items-item-sel" :class="[ item.id > 0 ? 'glance-shop-cart-itemselected-img':'glance-shop-cart-itemunselected-img']"
								 @click="clickitemselected(item.id)"></view>
							</view>
							<!-- 图片 -->
							<view style="width: 30%;height: 100%;text-align:center;">
								<image src="../../static/logo.png" mode="scaleToFill" style="height: 75px;width: 75px;line-height: 80px;padding-top: 5px;"
								 @click="clickitemhref(item.href)"></image>
							</view>
							<!-- 描述 -->
							<view class="glance-shop-cart-items-item-des">
								<!-- 名称 -->
								<view class="sigle-line-text" style="font-size: 16px;height: 33.33%;text-align: left;" @click="clickitemhref(item.href)">{{ item.name }}</view>
								<!-- 属性 -->
								<view class="sigle-line-text" style="font-size: 13px;height: 33.33%;text-align: left;color: #ADADAD;" @click="clickitemhref(item.href)">{{ item.attributes }}</view>
								<!-- 价格 & 数量-->
								<view class="glance-shop-cart-items-item-pq">
									<view class="sigle-line-text" style="font-size: 15px;text-align: left;width: 50%;">￥{{item.price}}</view>
									<!-- 数量操作 -->
									<view class="glance-shop-cart-items-item-opt">
										<!-- 减数量 -->
										<view class="glance-shop-cart-items-item-opt-quantity-minus" @click="minusitem(item.id)">-</view>
										<!-- 数量 -->
										<view class="glance-shop-cart-items-item-opt-quantity">{{ item.quantity }}</view>
										<!-- 加数量 -->
										<view class="glance-shop-cart-items-item-opt-quantity-plus" @click="plusitem(item.id)">+</view>
									</view>
								</view>
							</view>
						</view>
					</view>

					<!-- 删除 -->
					<view class="glance-shop-cart-del" @click="clickdel(item.id)">
						<view class="glance-shop-cart-del-img"></view>
					</view>

				</scroll-view>
			</view>
			<view style="height: 10px;background-color: #F5F5F5;"></view>
		</view>
		<!-- 购买此商品的人还买了 -->
		<view style="height: 750px;background-color: #FFFFFF;" v-if="cntitems>0">购买此商品的人还买了</view>
		<view style="height: 10px;background-color: #F5F5F5;"></view>
		<!-- 猜你喜欢 -->
		<view style="height: 750px;background-color: #FFFFFF;">猜你喜欢</view>

		<view style="height: 10px;background-color: #F5F5F5;"></view>
		<!-- 金额合计 -->
		<view class="glance-shop-cart-order">
			<!-- 全选 -->
			<view style="width: 12%;">
				<!-- 勾选 -->
				<view class="glance-shop-cart-scrollx-items-item-sel" :class="[ isselectedall ? 'glance-shop-cart-itemselected-img':'glance-shop-cart-itemunselected-img']"
				 @click="clickitemselectedall"></view>
			</view>
			<!-- 总数量 -->
			<view class="glance-shop-cart-total-cnt">已选：( {{ cntitems }} )</view>
			<!-- 总金额 -->
			<view class="glance-shop-cart-total-amt">￥{{ totalamount }}</view>
			<!-- 立即下单 -->
			<view class="glance-shop-cart-create-order" @click="createorder">立即下单</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				// 全选 默认全选
				isselectedall: true,
				// scroll position
				scrollposition: 0,
				// 实际项目中 购物车数据可从本地缓存中获取或从服务端获取
				cart: [],
				// 订单商品 购物车里已选择的商品生成订单
				ordercart: [],
				// 购买更多
				buymore: [],
				// 为您推荐的商品
				itemrecommend: [],
				// 合计金额
				totalamount: 0,
				// 购物车商品数量 
				cntitems: 0,
				// 显示空购物车背景
				shownullcart: false,
				food:[]
			};
		},
		onShow: function() {
			// 从缓存或服务端获取到购物车商品 这里示例数据如下：
			try {
				const value = uni.getStorageSync("food");
				if (value) {

					console.log(value)
					this.food = value

				}
			} catch (e) {
				//错误
			}
			// 从缓存或服务端获取到购物车商品 这里示例数据如下：
			let objcart = [{
				'groupkey': '',
				'items': []
			}]
			objcart[0].items = this.food
			// 这里示例数据对象存入缓存
			try {
				uni.setStorageSync('cart', objcart);
			} catch (e) {
				// error
			}
			// 从缓存获取
			try {
				const objcart = uni.getStorageSync('cart');
				if (objcart) {
					// 装载this cart
					this.cart = objcart;
				}
			} catch (e) {
				// error
			}

			// console.log(this.cart)
			// 默认勾选购物车所有商品 合计金额 合计数量
			for (let i = 0; i < this.cart.length; i++) {
				for (let k = 0; k < this.cart[i].items.length; k++) {
					// 总金额 
					this.totalamount = this.totalamount + this.cart[i].items[k].price * this.cart[i].items[k].quantity
					// 总数量
					this.cntitems = this.cntitems + this.cart[i].items[k].quantity
				}
			}
			this.totalamount = this.fmamount(this.totalamount)
		},
		// 下拉刷新
		onPullDownRefresh() {
			uni.startPullDownRefresh(this.cart)
		},
		// 停止刷新
		onPullDownRefresh() {
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);

		},
		computed: {},
		// 关闭购物车前 存入缓存
		beforeDestroy: function() {
			// 购物车数据更新缓存
			try {
				uni.setStorageSync('cart', this.cart);
			} catch (e) {
				// error
			}
		},
		methods: {
			// scroll x 归位
			scrollhoming() {
				this.scrollposition = this.scrollposition - 1
				// console.log(this.scrollposition)
			},
			// scroll x 归位后重置 scrollposition
			scrollhomed() {
				this.scrollposition = 0
				// console.log(this.scrollposition)
			},
			// 点击全选
			clickitemselectedall() {
				this.isselectedall = !this.isselectedall
				// 全选
				if (this.isselectedall) {
					if (this.cart) {
						for (var i = 0; i < this.cart.length; i++) {
							for (let k = 0; k < this.cart[i].items.length; k++) {
								// 未选的则选
								if (this.cart[i].items[k].id < 0) {
									this.cart[i].items[k].id = -this.cart[i].items[k].id
									// 更新总数量
									this.cntitems = this.cntitems + this.cart[i].items[k].quantity
									// 更新总金额
									this.totalamount = this.totalamount + this.cart[i].items[k].price * this.cart[i].items[k].quantity
									this.totalamount = this.fmamount(this.totalamount)
								}
							}
						}
					}
				} else {
					// 全不选
					// this.cntitems = 0
					if (this.cart) {
						for (var i = 0; i < this.cart.length; i++) {
							for (let k = 0; k < this.cart[i].items.length; k++) {
								// 累计总金额和总数量 勾选时加
								if (this.isselected(this.cart[i].items[k].id)) {
									this.cart[i].items[k].id = -this.cart[i].items[k].id
									// 更新总数量
									this.cntitems = this.cntitems - this.cart[i].items[k].quantity
									// 更新总金额
									this.totalamount = this.totalamount - this.cart[i].items[k].price * this.cart[i].items[k].quantity
									this.totalamount = this.fmamount(this.totalamount)
								}
							}
						}
					}
				}
			},
			// 点击勾选
			clickitemselected(id) {
				if (this.cart) {
					for (var i = 0; i < this.cart.length; i++) {
						for (let k = 0; k < this.cart[i].items.length; k++) {
							if (this.cart[i].items[k].id == id) {
								this.cart[i].items[k].id = -this.cart[i].items[k].id
								// 累计总金额和总数量 勾选时加
								if (this.isselected(this.cart[i].items[k].id)) {
									// 更新总数量
									this.cntitems = this.cntitems + this.cart[i].items[k].quantity
									// 更新总金额
									this.totalamount = this.totalamount + this.cart[i].items[k].price * this.cart[i].items[k].quantity
									this.totalamount = this.fmamount(this.totalamount)
									// 最后已勾选则 全选
									if (this._isselectedall()) {
										this.isselectedall = true
									}
								} else {
									// 取消勾选时减
									// 更新总数量
									this.cntitems = this.cntitems - this.cart[i].items[k].quantity
									// 更新总金额
									this.totalamount = this.totalamount - this.cart[i].items[k].price * this.cart[i].items[k].quantity
									this.totalamount = this.fmamount(this.totalamount)
									this.isselectedall = false
								}
								return
							}
						}
					}
				}
			},
			// 点击删除
			clickdel(itemid) {
				for (let i = 0; i < this.cart.length; i++) {
					for (let k = 0; k < this.cart[i].items.length; k++) {
						if (this.cart[i].items[k].id == itemid) {
							// 勾选状态下更新数量和金额
							if (this.isselected(this.cart[i].items[k].id)) {
								// 更新总数量
								this.cntitems = this.cntitems - this.cart[i].items[k].quantity
								// 更新总金额
								this.totalamount = this.totalamount - this.cart[i].items[k].price * this.cart[i].items[k].quantity
								this.totalamount = this.fmamount(this.totalamount)
								// 删除商品
								this.cart[i].items[k].id = -99 - itemid
							} else {
								// 未勾选时 删除商品 列表中不再显示 id= -99-原始id
								this.cart[i].items[k].id = -99 + itemid
							}
							// 如果全部删除 则显示空购物车背景
							if (this._isdeledall()) {
								this.shownullcart = true
								this.isselectedall = false
							}
							return
						}
					}
				}
				// 删除购物车商品时 更新合计金额

			},
			// 减数量
			minusitem(itemid) {
				for (let i = 0; i < this.cart.length; i++) {
					for (let k = 0; k < this.cart[i].items.length; k++) {
						if ((this.cart[i].items[k].id == itemid) && (this.cart[i].items[k].quantity > 0)) {
							// 更新item数量
							this.cart[i].items[k].quantity = this.cart[i].items[k].quantity - 1
							// 勾选状态下更新数量和金额
							if (this.isselected(this.cart[i].items[k].id)) {
								// 更新总数量
								this.updatecntitems(-1)
								// 更新总金额
								this.updatetotalamt(-this.cart[i].items[k].price)
							}
							// 数量减为0时 不勾选
							if (this.cart[i].items[k].quantity == 0) {
								this.cart[i].items[k].id = this._unselected(this.cart[i].items[k].id)
							}
							return
						}
					}
				}
			},
			// 加数量
			plusitem(itemid) {
				for (let i = 0; i < this.cart.length; i++) {
					for (let k = 0; k < this.cart[i].items.length; k++) {
						// 这里需要进行超卖控制 商品可售卖的数量 这里面示例可售卖100
						if ((this.cart[i].items[k].id == itemid) && (this.cart[i].items[k].quantity < 100)) {
							// 更新item数量
							this.cart[i].items[k].quantity = this.cart[i].items[k].quantity + 1
							// 勾选状态下更新数量和金额
							if (this.isselected(this.cart[i].items[k].id)) {
								// 更新总数量
								this.updatecntitems(1)
								// 更新总金额
								this.updatetotalamt(this.cart[i].items[k].price)
							} else {
								// 加数量时未勾选则 勾选
								this.cart[i].items[k].id = this._selected(this.cart[i].items[k].id)
								// 更新总数量、
								this.cntitems = this.cntitems + this.cart[i].items[k].quantity
								// 更新总金额
								this.totalamount = this.totalamount + this.cart[i].items[k].price * this.cart[i].items[k].quantity
								this.totalamount = this.fmamount(this.totalamount)
							}

							// 最后已勾选则 全选
							if (this._isselectedall()) {
								this.isselectedall = true
							}
							return
						}
					}
				}
			},
			// 点击商品href
			clickitemhref(str) {
				uni.showModal({
					content: '点击了商品链接' + str
				})
			},
			// 点击了营销活动
			clickgroupkey(str) {
				let mehref = this.getgroupkeyhref(str)
				uni.showModal({
					content: '点击了营销活动' + mehref
				})
			},
			// 更新合计金额
			updatetotalamt(amt) {
				this.totalamount = this.totalamount + amt
				this.totalamount = this.fmamount(this.totalamount)

			},
			// 更新合计数量
			updatecntitems(cnt) {
				this.cntitems = this.cntitems + cnt
			},
			// 格式化金额
			fmamount(amt) {
				return Math.round(amt * 100) / 100
			},
			// 不勾选
			_unselected(id) {
				return -Math.abs(id)
			},
			// 勾选
			_selected(id) {
				return Math.abs(id)
			},
			// 是否勾选
			isselected(id) {
				return id > 0
			},
			// 是否全部已勾选
			_isselectedall() {
				for (let i = 0; i < this.cart.length; i++) {
					for (let k = 0; k < this.cart[i].items.length; k++) {
						// 存在一个未勾选 则未全选
						if (this.cart[i].items[k].id < 0) {
							return false
						}
					}
				}
				return true
			},
			// 是否全部删除
			_isdeledall() {
				for (let i = 0; i < this.cart.length; i++) {
					for (let k = 0; k < this.cart[i].items.length; k++) {
						// 存在 未删除
						if (this.cart[i].items[k].id > -99) {
							return false
						}
					}
				}
				return true
			},
			// 获取营销页面链接
			getgroupkeyhref(str) {
				// 这里通过营销活动名称 获取营销活动页面 需要自己实现
				return '营销活动页面'
			},
			// 生成订单
			createorder() {
				// 合计金额大于0 创建订单
				if (this.totalamount == 0) {
					uni.showModal({
						content: '请选择下单的商品！'
					})
				} else {
					// 1、处理购物车内已选择的订单生成商品
					for (let i = 0; i < this.cart.length; i++) {
						for (let k = 0; k < this.cart[i].items.length; k++) {
							// item id 大于0 的是勾选的
							if (this.cart[i].items > 0) {
								//这里的item 是下单的
							}
						}
					}
					// 2、生成订单成功后 删除购物车内已生成订单的商品
					wx.removeStorageSync('food')
					wx.removeStorageSync('cart')
				}
			}
		}
	}
</script>

<style lang="scss">
	// 单行文本样式
	.sigle-line-text {
		width: 100%;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		text-align: left;
	}

	.glance-shop-cart {
		width: 100%;
		// font-family: Arial, Helvetica, sans-serif;
	}

	// 滑动item 样式
	.glance-shop-cart-scrollx-items {
		width: 100%;
		height: 90px;
		display: flex;
		display: -webkit-flex;
		flex-flow: row nowrap;
		align-items: flex-start;
		justify-content: space-between;
		// padding: 10px 0 10px 0;
		margin-top: 10px;
	}

	// 滑动区域内item 样式
	.glance-shop-cart-scrollx-items-item {
		display: flex;
		display: -webkit-flex;
		flex-flow: row nowrap;
		align-items: flex-start;
		justify-content: flex-start;
		width: 100%;
		height: 100%;
	}

	// item 勾选样式
	.glance-shop-cart-scrollx-items-item-sel {
		position: relative;
		top: 30%;
		left: 60%;
		;
	}

	// item 描述信息样式
	.glance-shop-cart-items-item-des {
		width: 60%;
		height: 100%;
		display: -webkit-flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: flex-start;
	}

	// 价格和数量样式
	.glance-shop-cart-items-item-pq {
		width: 100%;
		height: 33.33%;
		display: flex;
		display: -webkit-flex;
		flex-flow: row nowrap;
		align-items: flex-start;
		justify-content: space-between;
	}

	// 数量操作
	.glance-shop-cart-items-item-opt {
		display: flex;
		display: -webkit-flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		width: 50%;
		height: 100%;
		font-size: 20px;
		margin-right: 10px;
		text-align: center;
		color: #6C6C6C;
		line-height: 100%;

		// 减数量
		&-quantity-minus {
			border-style: solid;
			border-width: 1px 0px 1px 1px;
			border-color: #E0E0E0;
			width: 33.33%;
			height: 80%;
			position: relative;
			top: 25%;
			transform: translateY(-50%)
		}

		// 数量
		&-quantity {
			border-style: solid;
			border-width: 1px 1px 1px 1px;
			border-color: #E0E0E0;
			width: 33.33%;
			height: 80%;
			font-size: 13px;
			position: relative;
			top: 25%;
			transform: translateY(-50%);
		}

		// 加数量
		&-quantity-plus {
			border-style: solid;
			border-width: 1px 1px 1px 0;
			border-color: #E0E0E0;
			width: 33.33%;
			height: 80%;
			position: relative;
			top: 25%;
			transform: translateY(-50%);
		}
	}

	// 底部汇总和订单样式
	.glance-shop-cart-order {
		width: 100%;
		height: 60px;
		background-color: #ffffff;
		display: flex;
		display: -webkit-flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		position: fixed;
		bottom: 0px;
	}

	// 合计金额样式
	.glance-shop-cart-total-cnt {
		width: 30%;
		text-align: left;
		line-height: 40px;
		font-size: 13px;
		margin-left: 15px;
	}

	// 合计金额样式
	.glance-shop-cart-total-amt {
		width: 35%;
		text-align: right;
		margin-right: 15px;
		line-height: 40px;
		font-size: 16px;
		margin-left: 10px;
		color: #f40;
	}

	// 生成订单样式
	.glance-shop-cart-create-order {
		width: 30%;
		background-color: #DC143C;
